@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.map {
  height: 100vh;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  z-index: 1;
  svg {
    height: 100%;
    width: 100%;
    top: 0;
    background-color: #222222;
    @include media($media-mobile...) {
      width: 200% !important;
      margin-left: -50%;
    }
  }
  &__wrapper {
    position: sticky;
    top: 0;
    overflow: hidden;
    height: 100vh;
    @include media($media-mobile...) {
      z-index: -1;
    }
  }
  h2 {
    left: 60px;
    top: 60px;
    color: #fff;
    position: absolute;
    top: 0;
    z-index: 10;
    top: 60px;
    width: calc(100% - 200px);
    span:first-child {
      position: absolute;
    }
    @include media($media-mobile...) {
      left: 0;
      top: 20px;
      margin-left: 20px;
      width: calc(100% - 20px);
      margin-top: 0;
      position: relative;
      margin-bottom: 0;
    }
  }
  .readers {
    opacity: 0;
  }
  svg text {
    opacity: 0;
    color: #222;
    pointer-events: none;
    transition: 0.2s ease-in;
  }
  svg circle {
    transition: 0.2s ease-in;
  }
  .markers circle:hover {
    // r: 40px;
    transform: scale(8);
  }
  .active .marker {
    animation: zoom__reporter infinite ease-in-out;
    animation-duration: 3s;
    // &:hover {
    //   animation: none;
    //   transition: 0.2s ease-in;
    //   transform: scale(8);
    // }
  }
  .active text {
    animation: opacity__reporter infinite ease-in-out;
    animation-duration: 3s;
  }
  .readers circle:hover {
    fill: rgba(16,231,255,1);
  }
  svg circle:hover {
    cursor: pointer;
  }
  svg circle:hover + text {
    opacity: 1;
  }
  .readers circle {
    animation: zoom infinite ease-in-out;
    animation-duration: 1s;
  }
  &__number {
    left: 60px;
    color: #fff;
    position: sticky;
    z-index: 10;
    top: 60vh;
    left: 50%;
    text-align: left;
    color: rgb(16,231,255);
    margin-top: 60vh;
    margin-bottom: calc(30vh - 120px);
    height: 0;
    div:last-child {
      font-size: 5vw;
      font-weight: 300;
      @include media($media-mobile...) {
        font-size: 8vw;
      }
    }
    div:first-child {
      font-size: 16px;
      letter-spacing: 0.1em;
      padding-left: 0.2em;
      font-weight: 700;
      @include media($media-mobile...) {
        padding-left: 0.2em;
      }
    }
    @include media($media-mobile...) {
      position: absolute;
      top: 58vh;
      left: 20px;
      width: calc(100% - 20px);
      margin-top: 0;
      
    }
  }
  &__context {
    position: absolute;
    width: calc(100% - 40px);
    top: 15vh;
    display: flex;
    margin: 0 20px;
    flex-wrap: wrap;
    text-align: left;
    color: #fff;
    z-index: 20;
  }
  &__item {
    width: 33.3%;
    margin-bottom: 20px;
    span {
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background-color: #FACC29;
      display: inline-block;
      margin-right: 6px;
      vertical-align: middle;
    }
    h3 {
      display: inline-block;
      font-size: 13.5px;
      margin-top: 0;
      vertical-align: middle;
      letter-spacing: 1px;
    }
    &.reader {
      span {
        background-color: rgb(16,231,255);
      }
    }
  }
}

.section_sub {
  position: absolute;
  height: 200vh;
  width: 100%;
}

@keyframes zoom {
  0% {
    transform: scale(0.5)
  }
  50% {
    transform: scale(1.5)
  }
  100% {
    transform: scale(0.5)
  }
}

@keyframes zoom__reporter {
  0% {
    transform: scale(1)
  }
  50% {
    transform: scale(8)
  }
  100% {
    transform: scale(1)
  }
}

@keyframes opacity__reporter {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
