@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.quote {
  height: 100vh;
  background-color: #222;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  overflow: hidden;
  h2 {
    position: absolute;
    left: 60px;
    top: 60px;
    color: #fff;
    @include media($media-mobile...) {
      left: 20px;
      top: 20px;
    }
  }
  &__slider {
    max-width: 800px;
    height: 60%;
    width: calc(80% - 120px);
    margin: 0 auto;
    &::before, &::after {
      content: '';
      background-image: url("data:image/svg+xml,%3Csvg width='60' height='46' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M60 0v10H10v36H0V0z' fill='%23FFF' fill-rule='nonzero'/%3E%3C/svg%3E");
      width: 60px;
      height: 60px;
      position: absolute;
      background-size: cover;
      opacity: 0.5;
      @include media($media-mobile...) {
        width: 30px;
        height: 30px;
      }
    }
    &:before {
      left: 0;
      top: 0;
      
    }
    &:after {
      right: 0;
      bottom: 0;
      transform: rotate(180deg);
      transform-origin: center;
      @include media($media-mobile...) {
        bottom: 24vh;
      }
    }
    .slick-list {
      background-color: rgba(216, 216, 216, 0.05);
    }
    @include media($media-mobile...) {
      width: calc(100% - 40px);
      height: 80%;
    }
  }
  &__handler {
    width: 90px;
    margin-left: auto;
    position: absolute;
    right: 60px;
    @include media($media-mobile...) {
      display: none;
    }
  }
  &__content {
    padding: 10%;
    height: 100%;
    box-sizing: border-box;
    display: flex !important;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    &:hover {
      cursor: grab;
    }
  }
  h3 {
    text-align: left;
    display: block;
    max-width: 500px;
    margin: 0 auto;
    line-height: 1.8;
  }
  &__author {
    font-family: 'Noto Serif TC', serif;
    font-size: 16px;
    letter-spacing: 0.25em;
    line-height: 1.5;
  }
  &__pagination {
    margin: 30px 0;
    padding-left: 15px;
  }
  @include media($media-mobile...) {
    align-items: flex-end;
  }
  &__current {
    display: none;
    @include media($media-mobile...) {
     display: block;
     position: absolute;
     bottom: 15vh;
     width: calc(50% - 20px);
     left: 20px;
     pointer-events: none;
    }
  }
}

.quote .slick-list {
  height: 100%;
  outline: none;
  @include media($media-mobile...) {
    height: 70%;
  }
}

.quote__handler {
  .slick-slide {
    opacity: 0.6;
    transform: scale(0.8);
    transition: 0.2s linear;
    &:after {
      content: '';
      height: 10px;
      width: 10px;
      position: absolute;
      left: 0;
      top: 50%;
      background-color: #fff;
      transform: translateY(-50%);
      border-radius: 50%;
    }
  }
  
  .slick-active ~ .slick-active ~ .slick-active {
    opacity: 1;
    transform: scale(1);
  }
  
  .slick-active ~ .slick-active ~ .slick-active ~ .slick-active {
    opacity: 0.6;
    transform: scale(0.8);
  }
  &:hover {
    cursor: -webkit-grab;
    cursor: grab;
  }
}

.slick-track {
  height: 100%;
  outline: none
}

.quote .slick-slide > div {
  height: 100%;
}

.slick-arrow {
  bottom: 0;
  top: auto;
  // @include media($media-desktop-tablet...) {
  //   display: none !important;
  // }
  transform: translateY(200%);
  @include media($media-mobile...) {
    bottom: 15vh;
  }
}

.slick-slide, .slick-slide *{ outline: none !important; }

.slick-prev {
  left: 50%;
  transform: translate(-150%, 200%);
  @include media($media-mobile...) {
    // left: 0;
    // transform: none;
    transform: translate(-150%, 0);
  }
}

.slick-next {
  right: 50%;
  transform: translate(150%, 200%);
  @include media($media-mobile...) {
    // left: 0;
    // right: auto;
    transform: translate(150%, 0);
  }
}