@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.membership {
  height: 100vh;
  display: flex;
  background-color: #222;
  &__wrapper {
    width: 150px;
    height: 200px;
    &:first-child {
      margin-left: calc(50% - 250px);
      margin-right: 100px;
    }
    &:last-child {
      margin-right: calc(50% - 250px);
      margin-left: 100px;
    }
    @include media($media-mobile...) {
      margin-left: auto !important;
      margin-right: auto !important;
      height: 200px;
    }
  }
  #pin-subscribe {
    z-index: 100;
    pointer-events: none;
    height: 500vh;
    position: absolute;
    width:50%;
    a {
      pointer-events: auto;
      text-decoration: none;
    }
    svg {
      transform-origin: left bottom;
      width: 100%;
      max-width: 100px;
      min-width: 50px;
      overflow: visible;
      position: sticky;
      left: 0;
      top: 50%;
      margin-top: 100px;
      @include media($media-mobile...) {
        margin-top: 0;
      }
    }
    @include media($media-mobile...) {
      width: 100%;
      top: 50vh;
      height: 450vh;
    }
  }
  #pin-download {
    width: 50%;
    position: absolute;
    right: 0;
    // bottom: 50%;
    svg {
      width: 100%;
      max-width: 100px;
      min-width: 50px;
      margin-top: 100px;
    }
    a {
      pointer-events: auto;
      text-decoration: none;
    }
    @include media($media-mobile...) {
      width: 100%;
      height: 50%;
    }
  }
  @include media($media-mobile...) {
    flex-direction: column;
  }
}