@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+TC:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;700&display=swap');
@import 'Styles/include-media';
@import 'Styles/mediaqueries';

html, body {
  margin: 0;
  height: 100%;
  overflow-scrolling: touch;
  background-color: #222;
}

.App {
  text-align: center;
  height: 100%;
  background-color: #222;
  &-logo {
    position: fixed;
    z-index: 10;
    right: 60px;
    top: 55px;
    @include media($media-mobile...) {
      display: none;
    }
  }
}

#root {
  height: 100%;
}

.content {
  height: 100vh;
}

.section {
  position: relative;
  // -webkit-transform: translate3d(0,0,0);
}

.module {
  position: relative;
}

h2 {
  font-size: 36px;
  letter-spacing: 0.5em;
  line-height: 1.4;
  font-family: "Hiragino Sans";
  font-weight: 500;
  margin: 0;
  text-align: left;
  width: calc(100% - 80px);
  @include media($media-mobile...) {
    font-size: 16px;
    width: calc(100% - 40px);
    font-weight: 600;
  }
  button {
    font-size: 36px;
    letter-spacing: 0.5em;
    line-height: 1.4;
    font-family: "Hiragino Sans";
    font-weight: 500;
    margin: 0;
    text-align: left;
    background: none;
    color: #fff;
    box-shadow: none;
    border: none;
    outline: none;
    padding: 0;
    opacity: 0.6;
    border-bottom: 1px solid #fff;
    &:hover {
      cursor: pointer;
    }
    &:active,&:focus,&:hover{
      color:#2bb6c9;
      border-bottom: 1px solid #2bb6c9;
      opacity: 1;
    }
    &.active {
      opacity: 1;
      border-bottom: none;
      &:hover {
        cursor: auto;
      }
    }
    @include media($media-mobile...) {
      font-size: 16px;
      font-weight: 600;
    }
  }
}

h3 {
  font-family: 'Noto Serif TC', serif;
  font-size: 24px;
  letter-spacing: 0.25em;
  line-height: 1.5;
  margin-bottom: 0;
  @include media($media-mobile...) {
    font-size: 16px;
  }
}

.round__button {
  font-size: 16px;
  letter-spacing: 0.3em;
  line-height: 1.5;
  text-decoration: none;
  position: relative;
  text-align: center;
  &:after {
    content: '';
    position: absolute;
    width: 75px;
    height: 75px;
    border-radius: 50%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); 
    border: 1px solid #fff;
    @include media($media-mobile...) {
      width: 53px;
      height: 53px;
    }
  }
  &:hover {
    color: #222;
    &:after {
      background-color: #fff;
    }
  }
  &:before {
    content: '';
    position: absolute;
    width: calc(30vw - 150px);
    max-width: 250px;
    height: 1px;
    right: 180%;
    background-color: #fff;
    top: 50%;
    transform: translateY(-50%);
    @include media($media-mobile...) {
      display: none;
    }
  }
  span {
    padding-left: 5px;
    display: block;
    z-index: 1;
    position: relative;
    @include media($media-mobile...) {
      font-size: 12px;
    }
  }
}

.desktop {
  @include media($media-mobile...) {
    display: none;
  }
}

.mobile {
  display: none !important;
  @include media($media-mobile...) {
    display: block !important;
  }
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

.media-controls-container, .media-controls-container * {
  display: none;
}
