.ml12, .ml13, .ml14, .ml15 {
  font-weight: 500;
  font-size: 2.25em;
  letter-spacing: 0.5em;
  color: #fff;
  position: absolute;
  top: 50%;
  z-index: 1000;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  margin: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
  overflow: hidden;
  @include media($media-mobile...) {
    font-size: 1.4em;
  }
}

.ml12 .letter, .ml13 .letter, .ml14 .letter, .ml15 .letter {
  display: inline-block;
  line-height: 1.5;
}

.ml12 {
  transform: translateX(-50%) translateY(-120%);
}

.ml13 {
  font-size: 1.2em;
  transform: translateX(-50%) translateY(20%);
  letter-spacing: 1em;
  width: auto;
  text-align: center;
  width: 100%;
  @include media($media-mobile...) {
    font-size: 1em;
    letter-spacing: 0.4em;
  }
}

@include media($media-mobile...) {
  .ml12 .letter {
    &:nth-child(6) {
      display:inline;
    }
    &:nth-child(6):after {
      content:"\a";
      white-space: pre;
    }
  }
  .ml13 .letter {
    &:nth-child(4) {
      display:inline;
    }
    &:nth-child(4):after {
      content:"\a";
      white-space: pre;
    }
    &:nth-child(5) {
      display:none;
    }
  }
  .ml14 .letter {
    &:nth-child(4) {
      display:inline;
    }
    &:nth-child(4):after {
      content:"\a";
      white-space: pre;
    }
    &:nth-child(5) {
      display:none;
    }
  }
  .ml15 .letter {
    &:nth-child(8) {
      display:inline;
    }
    &:nth-child(8):after {
      content:"\a";
      white-space: pre;
    }
  }
}

.cover {
  width: 100%;
  height: 100vh;
  position: relative;
  z-index: 2;
  background-color: #000;
  &__video, &__image {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
    background-size: cover;
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
  }
  &:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    top: 0;
    left: 0;
  }
}

#progress {
  position: fixed;
  top: 10vh;
}
