@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.honour {
  min-height: 100vh;
  height: auto;
  background: #222;
  color: #fff;
  // display: flex;
  // justify-content: center;
  // align-items: flex-start;
  padding: 0;
  box-sizing: border-box;
  h2 {
    position: absolute;
    left: 60px;
    top: 60px;
    color: #fff;
    z-index: 1;
    @include media($media-mobile...) {
      left: 20px;
      top: 20px;
      width: calc(70% - 40px)
    }
  }
  h4 {
    font-weight: 900;
    font-size: 14px;
    letter-spacing: 14px;
    color: #222;
    background-color: #fff;
    text-align: center;
    display: inline-block;
    padding: 0 7px 0 14px;
    margin-bottom: 1.5em;
    margin-top: 0;
  }
  &__box {
    text-align: left;
    max-width: 800px;
    margin: 0 auto;
    padding-top: 150px;
    position: relative;
    width: calc(100% - 420px);
    height: calc(100vh - 100px);
    overflow-y: scroll;
    overflow-x: hidden;
    @include media($media-mobile...) {
      width: calc(100% - 40px);
      padding-top: 15vh;
      height: 85vh;
    }
  }
  &__yearfilter {
    position: absolute;
    top: 150px;
    left: 60px;
    text-align: left;
    height: 45vh;
    display: flex;
    flex-direction: column;
    @include media($media-mobile...) {
      display: none;
    }
  }
  &__categoryfilter {
    position: absolute;
    bottom: 60px;
    /* display: flex; */
    right: 75px;
    top: 150px;
    height: 70vh;
    text-align: left;
    /* display: flex; */
    /* flex-direction: column; */
    /* min-height: 45vh; */
    @include media($media-mobile...) {
      display: none;
    }
    // .honour__button {
    //   text-align: right;
    //   margin-left: auto;
    // }
  }
  &__button {
    transition: 0.2s opacity linear;
    opacity: 0.6;
    margin-bottom: 24px;
    background: none;
    border: none;
    outline: none;
    box-shadow: none;
    color: #fff;
    padding: 0;
    font-size: 16px;
    display: block;
    text-align: left;
    &.active {
      font-weight: 900;
      opacity: 1;
      color: #fff;
    }
    // &:hover {
    //   cursor: pointer;
    //   opacity: 1;
    // }
    // &:focus {
    //   opacity: inherit;
    //   opacity: 1;
    // }
    // &:visited {
    //   color: #fff;
    //   opacity: 1;
    // }
  }
  &__item {
    padding-bottom: 3rem;
  }
  &__title {
    font-family: 'Noto Serif TC', serif;
    font-size: 24px;
    letter-spacing: 0.25em;
    line-height: 1.5;
    margin-bottom: 0;
    font-weight: 400;
    color: #fff;
    text-decoration: none;
    &:lang(en) {
      letter-spacing: 1px;
    }
    @include media($media-mobile...) {
      font-size: 14px;
      letter-spacing: 0.15em;
    }
  }
  a.honour__title {
    border-bottom: 1px solid #fff;
    &:active, &:focus, &:hover{
      color:#2bb6c9;
      border-bottom: 1px solid #2bb6c9;
    }
  }
  &__name {
    font-size: 36px;
    letter-spacing: 0.25em;
    line-height: 1;
    font-family: 'Noto Serif TC', serif;
    font-weight: 700;
    margin-top: 0;
    margin-bottom: 0.4em;
    &:lang(en) {
      letter-spacing: 1px;
    }
    @include media($media-mobile...) {
      font-size: 16px;
      letter-spacing: 0.15em;
    }
  }
  &__control {
    position: sticky;
    width: 100%;
    height: 100%;
    top: -1px;
    left: 60px;
    z-index: 1;
    &:after {
      content: '';
      position: absolute;
      height: 110px;
      width: 100%;
      top: 0;
      background-color: #222;
      left: 0;
      @include media($media-mobile...) {
        height: 85px;
      }
    }
  }
  &__control__mobile {
    position: absolute;
    top: 20px;
    right: 20px;
    display: none;
    width: auto;
    z-index: 10;
    flex-direction: column;
    align-items: flex-end;
    .dd__wrapper:first-child {
      margin-bottom: 5px;
      display: flex;
      justify-content: flex-end;
    }
    &.active {
      display: flex;
    }
    @include media($media-desktop-tablet...) {
      display: none;
      &.active {
        display: none;
      }
    }
  }
}

.dd__option {
  font-size: 14px;
  padding: 6px 4px;
}

.dd__expandIcon {
  margin-left: 10px;
  fill: #fff;
}

.dd__selectControl {
  background-color: transparent;
  border-color: #fff;
  color: #fff;
  padding: 2px 4px;
}

.dd__list {
  background-color: #222;
  color: #fff;
  border-color: #fff;
  right: 0;
}

.dd__selectedOption {
  color: #222;
  background-color: #888;
}

.dd__option:hover {
  color: #333;
}