@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.container {
  width: 100%;
  // height: 100%;
  // flex-wrap: no-wrap;
  // margin-top: 100vh;
  height: 1150vh;
  position: relative;
  .panel__wrapper {
    height: 100vh;
    position: sticky;
    top: 0;
    width: 100%;
  }
}


.panel {
	width: 100%;
  height: 100vh;
  font-weight: 600;
  font-size: 1.5em;
  text-align: center;
  color: white;
  box-sizing: border-box;
  will-change: transform;
  top: 0;
  left: 0;
  transition: opacity 300ms ease 0s, visibility 300ms ease 0s !important;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  &.active {
    opacity: 1;
    visibility: visible;
  }
  &__content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  
  h3 {
    text-align: left;
    margin-bottom: 24px;
    a {
      color: #fff;
      text-decoration: none;
      border-bottom: 1px solid #fff;
    }
    a:active,a:focus,a:hover{
      color:#2bb6c9;
      border-bottom: 1px solid #2bb6c9;
    }
  }
  h4 {
    font-weight: 900;
    font-size: 14px;
    letter-spacing: 3em;
    text-align: center;
    display: inline-block;
    margin-bottom: 3em;
    margin-top: 0;
    padding-left: 0.3em;
    @include media($media-mobile...) {
      letter-spacing: 4em;
      margin-bottom: 2em;
      padding-left: 0;
    }
  }
  // &__wrapper {
  //   width: 100vw;
  //   height: 100%;
  //   position: absolute;
  // }
  &__title {
    z-index: 1;
  }
  &__section {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    color: #fff;
    padding: 60px;
    box-sizing: border-box;
    background-color: rgba(0,0,0,0.2);
    padding-right: 65px;
    @include media($media-mobile...) {
      padding: 20px;
      padding-bottom: 120px;
    }
  }
  &__subtitle {
    text-align: left;
    width: calc(70vw - 60px);
  }
  &__link {
    color: #fff;
  }
  &__region {
    font-size: 72px;
    letter-spacing: 0.25em;
    line-height: 0.67;
    font-family: 'Noto Serif TC', serif;
    font-weight: 700;
    @include media($media-mobile...) {
      font-size: 32px;
    }
  }
  &__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    background-color: #222222;
    background-size: cover;
    background-position: center;
    left: 0;
    top: 0;
    @include media('portrait') {
      flex-direction: column;
    }
  }
  &__item {
    width: 25%;
    background-size: cover;
    background-position: center;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0;
      background-color: #222;
      left: 0;
      transition: 0.2s ease-in;
    }
    &:hover:after, &:focus:after {
      opacity: 0.2;
    }
    @include media('portrait') {
      height: 25%;
      width: 100%;
      &:nth-child(3) {
        background-image: url('../Images/multi4_m.jpg') !important;
        order: 4;
      }
      &:nth-child(4) {
        background-position: 50% 20%;
      }
    }
    @include media($media-mobile...) {
      
    }
  }
  &.member {
    .panel__item {
      &:first-child {
        background-color: #01506F;
      }
    }
  }
  &.member {
    display: flex !important;
    @include media($media-mobile...) {
      flex-direction: column;
    }
  }
  &__left {
    width: 60%;
    background-color: #333333;
    padding-top: 30vh;
    padding-left: 60px;
    padding-bottom: 60px;
    padding-right: 60px;
    box-sizing: border-box;
    &__item {
      text-align: left;
      img {
        height: 40px;
        max-width: 100%;
      }
    }
    h3 {
      margin-bottom: 10px;
    }
    @include media($media-mobile...) {
      width: 100%;
      padding-top: 10vh;
      height: 60%;
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  &__right {
    width: 40%;
    background-color: #FACC29;
    padding-top: 30vh;
    padding-right: 60px;
    padding-bottom: 60px;
    padding-left:10%;
    color: #333;
    box-sizing: border-box;
    &__item {
      text-align: left;
      img {
        
        max-width: 50%;
      }
    }
    h3 {
      margin-bottom: 16px;
      @include media($media-mobile...) {
        margin-bottom: 8px;
      }
    }
    @include media($media-mobile...) {
      width: 100%;
      height: 40%;
      padding-top: 20px;
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}

.panel:last-child {
  .panel__section {
    background-color: transparent;
  }
}
.panel:nth-child(4) .panel__bg {
  background-position: center top;
  @include media($media-mobile...) {
    background-position: 58% 50%;
  }
}
.panel:nth-child(6) .panel__bg {
  @include media($media-mobile...) {
    background-position: 67% 50%;
  }
}
.panel:nth-child(7) .panel__bg {
  @include media($media-mobile...) {
    background-position: 20% 50%;
  }
}
.panel:nth-child(8) .panel__bg {
  @include media($media-mobile...) {
    background-position: 44% 50%;
  }
}
